import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router';
import ArticlesList from '../components/ArticlesList';
import articleContent from './article-content';
import NotFoundPage from './NotFoundPage';
import CommentsList from '../components/CommentsList';
import UpvotesSection from '../components/UpvotesSection';
import AddCommentForm from '../components/AddCommentForm';

const ArticlesPage = () => {
    const {name} = useParams();
    const str = {name};
    const content = articleContent.find(para => para.name === str.name);
    const otherArticles = articleContent.filter(article => article.name !== str.name);

    const [articlesInfo, setArticleInfo] = useState({ upvotes: 0, comments : [] });
    
    useEffect(() => {
        const fetchData = async () => {
            const result = await fetch(`/api/articles/${str.name}`);
            const body = await result.json();
            setArticleInfo(body);
        }
        fetchData();
    }, [str.name]);

    if(!content) return <NotFoundPage />
    
    return (
        <>
            <h1>{content.title}</h1>
            <UpvotesSection articleName={str.name} 
            upvotes={articlesInfo.upvotes} setArticleInfo={setArticleInfo}/>
            
            {/* <p>This post has been upvoted {articlesInfo.upvotes} times! </p> */}
            {content.content.map((para, key) => (
               <p key={key}>{para}</p> 
            ))}

            <CommentsList comments={articlesInfo.comments} />
            <AddCommentForm articleName={str.name} setArticleInfo={setArticleInfo}/>
            
            <h3>Other Articles: </h3>
            <ArticlesList articles={otherArticles}/>
        </>
    );
}

export default ArticlesPage;