import React from 'react';

const HomePage = () => (
    <>
        <h1>Welcome to my blog!!</h1>
        <p>
            A welcome message is a message you send to customers once they land on your website or create an account with your service to greet and
            tell them more about your business.
        </p>

        <p>
            At the same time, greeting the new customer is neither the only nor the primary thing a website welcome message can do.
            There are many other great goals that you can achieve with a wisely crafted welcome message.
            In fact, the format, content, style, and design should pursue a definite goal that starts the user’s journey across your website.
        </p>

        <p>
            Often, welcome messages are delivered by email, however, you can also use live chat, in-app messaging or even a third-party messenger,
            such as Facebook or Slack to send them.
        </p>

        <p>
            Many businesses send welcome messages to customers, and some of them do it in a truly brilliant way.
            Browse our collection of the best website welcome message examples that can inspire you to make a unique message inviting your customers to start on a long journey with you.
            Each message in our list is designed to achieve a certain goal, and you are going to see how they do it.
        </p>
    </>
    
);

export default HomePage;